    margin-right: 0.25em;
    
    border: none;
    background-color: #22223b00;
    color: #fefefe;
    margin-top: -30px;

  }
  #cancel:hover {
    background-color: #4a4e6900;
    color: #c4bebe;
  }

  #post_button{
    position: absolute;
    margin-top: -30px;
  }
